import React from "react";
import Layout from "../components/layout";
import SEO from "../components/seo";
import ShowcaseCard from "../components/showcase-card";
import ShowcaseCodeKit from "../components/showcase-code-kit";
import CTA from "../components/cta";
import { useStaticQuery, graphql } from "gatsby";
import webflow from "../images/showcase/logos/webflow-logo.svg";
import hubspot from "../images/showcase/logos/hubspot-logo.svg";
import liquidjs from "../images/showcase/logos/liquidjs-logo.svg";

const Showcase = () => {
  const data = useStaticQuery(graphql`
    {
      showcasePolygonum: file(
        relativePath: { eq: "showcase/showcase-polygonum-online.png" }
      ) {
        childImageSharp {
          fluid(maxWidth: 520, quality: 100) {
            ...GatsbyImageSharpFluid
          }
        }
      }
      showcaseGoFloaters: file(
        relativePath: { eq: "showcase/showcase-go-floaters.png" }
      ) {
        childImageSharp {
          fluid(maxWidth: 520, quality: 100) {
            ...GatsbyImageSharpFluid
          }
        }
      }
      showcasePlanable: file(
        relativePath: { eq: "showcase/showcase-planable.png" }
      ) {
        childImageSharp {
          fluid(maxWidth: 520, quality: 100) {
            ...GatsbyImageSharpFluid
          }
        }
      }
      showcaseLandify: file(
        relativePath: { eq: "showcase/showcase-landify.png" }
      ) {
        childImageSharp {
          fluid(maxWidth: 520, quality: 100) {
            ...GatsbyImageSharpFluid
          }
        }
      }
    }
  `);
  return (
    <Layout>
      <SEO
        title="Showcase - Landify UI Kit for Figma"
        description="Check out some of the landing pages created with Landify UI kit showcased here. Submit your work and become a part of the showcase."
      />
      <div className="bg-accent-pastel-blue">
        <div className="container mx-auto">
          <div className="flex flex-col sm:py-24 py-12">
            <div className="flex flex-col">
              <h1 className="text-center">Made with Landify UI Kit</h1>
              <div className="flex flex-col xl:w-1/2 lg:w-2/3 w-full self-center">
                <p className="text-lg text-center py-4">
                  Check out some of the landing pages created with Landify UI
                  kit by changing typeface, colors, and combining multiple
                  blocks.
                </p>
                <p className="text-lg text-center">
                  <a
                    className="font-medium text-brand-purple-900"
                    href="https://forms.gle/HWF5ocfqXZosvrCs5"
                    target="_blank"
                    rel="noreferrer">
                    Submit your work
                  </a>{" "}
                  and become a part of showcase.
                </p>
              </div>
            </div>
          </div>
          <div className="grid sm:grid-cols-2 grid-cols-1 lg:col-gap-20 md:col-gap-8 col-gap-4 row-gap-20 pb-20 justify-self-center">
            <ShowcaseCard
              link="https://polygonum.online/"
              cover={data.showcasePolygonum.childImageSharp.fluid}
              title="Polygonum Online"
              designer="Nikita Marcius"
            />
            <ShowcaseCard
              link="https://gofloaters.com/teams/"
              cover={data.showcaseGoFloaters.childImageSharp.fluid}
              title="GoFloaters for Teams"
              designer="GoFloaters"
            />
            <ShowcaseCard
              link="https://www.figma.com/community/file/959025392861790796/Planable---Event-planning-landing-page"
              cover={data.showcasePlanable.childImageSharp.fluid}
              title="Planable - Event planning landing page"
              designer="Pramod Poudel"
            />
            <ShowcaseCard
              link="https://aravind.design"
              cover={data.showcaseLandify.childImageSharp.fluid}
              title="Project management app website"
              designer="Aravind Little Jack"
            />
          </div>
          <hr className="w-full border-0 bg-content-gray-700 h-px"></hr>
          <h3 className="pt-20 pb-8">
            Code kit made by developers with love{" "}
            <span role="img" aria-label="purple heart">
              ❤️
            </span>
          </h3>
          <div className="grid sm:grid-cols-3 grid-cols-1 lg:col-gap-8 col-gap-4 row-gap-8 pb-20 justify-self-center">
            <ShowcaseCodeKit
              link="https://marketplace.hubspot.com/products/inboundlabs/startup-framework-marketplace"
              logo={hubspot}
              platform="Hubspot"
              developer="InboundLabs"
            />
            <ShowcaseCodeKit
              link="https://webflow.com/website/landifyy"
              logo={webflow}
              platform="Webflow"
              developer="Hadic"
            />
            <ShowcaseCodeKit
              link="https://github.com/hey-fk/landify-landing-page-template"
              logo={liquidjs}
              platform="LiquidJS"
              developer="Fatma Kiraz"
            />
          </div>
        </div>
      </div>
      <CTA />
    </Layout>
  );
};

export default Showcase;
